<template>
  <div class="order-query-page">
    <!-- 页面头部 -->
    <div class="page-header">
      <div class="header-content">
        <h1 class="page-title">
          <i class="fas fa-search"></i>
          订单查询
        </h1>
        <p class="page-subtitle">管理和查看所有订单信息</p>
      </div>
      <div class="page-actions">
        <button class="action-btn export-btn" @click="exportData" title="导出数据">
          <i class="fas fa-file-export"></i>
          <span>导出</span>
        </button>
        <button class="action-btn refresh-btn" @click="refreshOrders" title="刷新数据">
          <i class="fas fa-sync-alt" :class="{ 'fa-spin': loading }"></i>
          <span>刷新</span>
        </button>
      </div>
    </div>





    <!-- 筛选器区域 -->
    <div class="filter-wrapper">
