}
.online-status.is-online { color: #059669; }
.online-status.is-online .online-dot { background-color: #10B981; }

.client-type-badge {
  color: #475569;
}

.ip-status.blocked {
  color: #DC2626;
  font-weight: 500;
}

.action-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
}

.table-action-btn {
  width: 28px; /* 缩小按钮尺寸 */
  height: 28px; /* 缩小按钮尺寸 */
  border-radius: 0.375rem;
