<template>
  <div class="login-container" :class="currentTheme">
    <!-- 主题切换按钮 -->
    <div class="theme-switcher">
      <button 
        v-for="theme in themes" 
        :key="theme.name"
        :class="['theme-btn', { active: currentTheme === theme.name }]"
        :style="{ background: theme.color }"
        @click="setTheme(theme.name)"
        :title="theme.label"
      >
        <i class="fas fa-check" v-if="currentTheme === theme.name"></i>
      </button>
    </div>

    <div class="login-box">
      <!-- 左侧装饰区域 -->
      <div class="login-decoration">
        <div class="decoration-content">
          <h1>通道市场管理系统</h1>
          <p>专业的支付通道管理平台</p>
          <div class="decoration-shapes">
            <div class="shape shape-1"></div>
            <div class="shape shape-2"></div>
            <div class="shape shape-3"></div>
          </div>
        </div>
      </div>

      <!-- 右侧登录表单 -->
      <div class="login-form-container">
        <div class="form-header">
          <h2>{{ isRegister ? '创建账号' : '欢迎回来' }}</h2>
          <p>{{ isRegister ? '请填写以下信息创建您的账号' : '请登录您的账号' }}</p>
        </div>

        <form @submit.prevent="handleSubmit" class="login-form">
          <div class="form-group">
            <label for="username">用户名</label>
            <div class="input-group">
              <i class="fas fa-user"></i>
              <input
                id="username"
                v-model="form.username"
                type="text"
                placeholder="请输入用户名"
                required
              >
            </div>
          </div>

          <div class="form-group">
            <label for="password">密码</label>
            <div class="input-group">
              <i class="fas fa-lock"></i>
              <input
                id="password"
                v-model="form.password"
                :type="showPassword ? 'text' : 'password'"
                placeholder="请输入密码"
                required
              >
              <i 
                class="fas toggle-password"
                :class="showPassword ? 'fa-eye-slash' : 'fa-eye'"
                @click="showPassword = !showPassword"
              ></i>
            </div>
          </div>

          <transition name="slide-fade">
            <div class="form-group" v-if="isRegister">
              <label for="email">邮箱</label>
              <div class="input-group">
                <i class="fas fa-envelope"></i>
                <input
                  id="email"
                  v-model="form.email"
                  type="email"
                  placeholder="请输入邮箱"
                  required
                >
              </div>
            </div>
          </transition>

          <transition name="slide-fade">
            <div class="form-group" v-if="isRegister">
              <label for="inviteCode">邀请码</label>
              <div class="input-group">
                <i class="fas fa-key"></i>
                <input
                  id="inviteCode"
                  v-model="form.inviteCode"
                  type="text"
                  placeholder="请输入邀请码"
                  required
                >
              </div>
            </div>
          </transition>

          <div class="form-options" v-if="!isRegister">
            <label class="checkbox-group">
              <input type="checkbox" v-model="form.rememberMe">
              <span class="checkbox-label">记住我</span>
            </label>
            <a href="#" class="forgot-link">忘记密码？</a>
          </div>

          <button 
            type="submit" 
            class="submit-button"
            :disabled="loading || !isFormValid"
          >
            <span v-if="!loading">{{ isRegister ? '注册' : '登录' }}</span>
            <div v-else class="loading-spinner"></div>
          </button>

          <div class="switch-mode">
            <p>
              {{ isRegister ? '已有账号？' : '还没有账号？' }}
              <a href="#" @click.prevent="switchMode(!isRegister)">
                {{ isRegister ? '立即登录' : '立即注册' }}
              </a>
            </p>
          </div>
        </form>

        <transition name="fade">
          <div v-if="error" class="error-alert">
            <i class="fas fa-exclamation-circle"></i>
            <span>{{ error }}</span>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
// 添加 EventBus 导入
import { EventBus } from '@/eventBus'

export default {
  name: 'Login',
  data() {
    return {
      isRegister: false,
      form: {
        username: '',
        password: '',
        email: '',
        rememberMe: false,
        inviteCode: ''
      },
      loading: false,
      error: null,
      showPassword: false,
      currentTheme: localStorage.getItem('theme') || 'default',
      themes: [
        { name: 'default', label: '默认主题', color: '#6366F1' },
        { name: 'ocean', label: '海洋主题', color: '#0EA5E9' },
        { name: 'forest', label: '森林主题', color: '#10B981' },
        { name: 'sunset', label: '日落主题', color: '#F59E0B' },
        { name: 'royal', label: '皇家主题', color: '#8B5CF6' }
      ]
    }
  },
  computed: {
    isFormValid() {
      const { username, password, email } = this.form;
      if (this.isRegister) {
        return username && password && email && this.isValidEmail(email);
      }
      return username && password;
    }
  },
  methods: {
    isValidEmail(email) {
      const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return re.test(email);
    },
    switchMode(isRegister) {
      this.isRegister = isRegister;
      this.error = null;
      this.form.email = '';
    },
    async handleSubmit() {
      try {
        this.loading = true;
        this.error = null;

        if (this.isRegister) {
          
          const response = await this.$api.post('/api/auth/register', {
            username: this.form.username,
            password: this.form.password,
            email: this.form.email,
            inviteCode: this.form.inviteCode
          });
          
          // 注册成功后切换到登录模式
          if (response.data.success) {
            this.$alert(response.data.message || '注册成功，请登录', '提示', {
              confirmButtonText: '确定',
              type: 'success',
              center: true,
              callback: () => {
                this.isRegister = false;
                this.error = null;
                // 保留用户名方便登录
                const username = this.form.username;
                this.form = {
                  username,
                  password: '',
                  email: '',
                  inviteCode: '',
                  rememberMe: false
                };
              }
            });
            return;
          } else {
            throw new Error(response.data.message);
          }
        } else {
          const response = await this.$api.post('/api/auth/login', {
            username: this.form.username,
            password: this.form.password
          });

          if (response.data.success) {
            // 保存所有必要的信息到 localStorage
            localStorage.setItem('token', response.data.token);
            localStorage.setItem('isLoggedIn', 'true');
            localStorage.setItem('username', response.data.username);
            localStorage.setItem('userRole', response.data.role);
            localStorage.setItem('currentUserId', response.data.userId);

            // 发送登录状态变更事件
            EventBus.$emit('login-state-changed', true);

            // 等待一小段时间确保状态更新
            await this.$nextTick();
            
            // 使用 replace 而不是 push 来避免重复航
            if (this.$route.path !== '/channel-market') {
              await this.$router.replace('/channel-market');
            }
          }
        }
      } catch (error) {
        // 忽略导航重复的错误
        if (error.name === 'NavigationDuplicated') {
          return;
        }
        console.error('Login error:', error);
        this.error = error.message || '登录失败，请稍后重试';
      } finally {
        this.loading = false;
      }
    },
    setTheme(themeName) {
      this.currentTheme = themeName;
      localStorage.setItem('theme', themeName);
    }
  },
  created() {
    if (localStorage.getItem('isLoggedIn') === 'true') {
      this.$router.push('/channel-market');
    }
    
    // 恢复保存的主题
    const savedTheme = localStorage.getItem('theme');
    if (savedTheme) {
      this.currentTheme = savedTheme;
    }
  }
}
</script>

